import React from 'react';
import { Modal } from 'react-bootstrap';
import ModalComponent from './modal';
import closedIcon from '../../images/icons/collapsible-open.svg';
import { graphql, useStaticQuery } from 'gatsby';
import Img from 'gatsby-image';

const PopupChannels = ({ show, setShow }) => {

  const data = useStaticQuery(graphql`
    query Channels {
      polythematicPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-polythematic"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      addOnsPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-add-ons"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      documentaryPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-documentary"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      hobbyPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-hobby"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      kidsPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-kids"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      moviesPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-movies"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      musicPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-music"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      newsPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-news"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      sportPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-sport"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      xxxPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-xxx"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  `)

  const allData = [
    {
      title: '',
      channels: data.polythematicPlaceholder,
    },
    {
      title: '',
      channels: data.newsPlaceholder,
    },
    {
      title: '',
      channels: data.moviesPlaceholder,
    },
    {
      title: '',
      channels: data.kidsPlaceholder,
    },
    {
      title: '',
      channels: data.sportPlaceholder,
    },
    {
      title: '  ',
      channels: data.hobbyPlaceholder,
    },
    {
      title: '',
      channels: data.musicPlaceholder,
    },
    {
      title: '',
      channels: data.documentaryPlaceholder,
    },
    {
      title: '  (18+)',
      channels: data.xxxPlaceholder,
    },
    {
      title: '',
      channels: data.addOnsPlaceholder,
    },

  ]

  return (
    <>
      <ModalComponent
        show={show}
        setShow={setShow}
      >
        <Modal.Header closeButton>
          <Modal.Title id="custom-modal-styling-title">
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className='px-sm-5'>
            <p>  </p>
            <h2 className='mb-4'>  </h2>

            {allData.map(({ title, channels }, index) => (
              <ChannelSection
                key={title + index}
                title={title}
                channels={channels}
              />
            ))}

          </div>
        </Modal.Body>
      </ModalComponent>
    </>
  );
}

const ChannelSection = ({ title, channels }) => {
  return (
    <>
      <CollapsibleHeader
        title={title}
        isOpen={true}
      />
      <div className='row'>
        <hr />
        {channels.edges.map(({ node }, index) => (
          <div key={index} className='col-4 col-sm-2 p-2 mb-5' style={{ maxHeight: 40 }}>
            <Img
              fluid={node.childImageSharp.fluid}
              style={{ maxHeight: 40 }}
              imgStyle={{ objectFit: "contain" }}
              alt=''
            />
          </div>
        ))}
      </div>
    </>)
}

const CollapsibleHeader = ({ title }) => {
  return (
    <>
      <div className='row w-100'>
        <div className='d-inline-block'>
          <img src={closedIcon} className='d-inline' alt='' />
          <h3 className={'d-inline'}>{title}</h3>
        </div>
      </div>

    </>
  )
}

export default PopupChannels;