import React, { useState } from 'react';
import Hero from '../components/business/hero';
import Layout from "../components/layout"
import { Link, useStaticQuery, graphql } from 'gatsby';
import SEO from "../components/seo";
import styled from 'styled-components';

import '../styles/business.css';
import Reasons from '../components/shared/reasons';
import constants from '../constants/business';
import BottomCta from '../components/shared/bottom-cta';
import TestimonialSlider from '../components/shared/testimonial-slider';
import CallCTA from '../components/business/call-cta';
import VerticalSteps from '../components/business/vertical-steps';
import CollapsibleSection from '../components/shared/collapsable';
import ClientLogosSection from '../components/business/client-logos-section';
import PopupChannels from '../components/shared/popup-channels';

const BusinessPage = () => {
  const [show, setShow] = useState(false);

  const data = useStaticQuery(graphql`
    query {
      bgImage: file(relativePath: {eq: "business/business-service-bg.png"}) {
        childImageSharp {
          fluid(maxHeight: 6056) {
            src
          }
        }
      }
      ctaBg: file(relativePath: {eq: "business/cta-bg.png"}) {
        childImageSharp {
          fluid(maxHeight: 838) {
            src
          }
        }
      }
      ctaPerson: file(relativePath: {eq: "business/call-cta-person.png"}) {
        childImageSharp {
          fluid(maxHeight: 1038) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <>
      <div className='business-page'>
        <BackgroundWrapper
          bg={data.bgImage.childImageSharp.fluid.src}
        >
          <Layout
            activeTab={'/za-biznesa'}
          >
            <SEO title=' ' />
            <Hero />

            <ClientLogosSection />

            <Reasons
              sectionTitle={<h2 className='text-white' style={{ whiteSpace: 'pre-wrap' }}>  <br />      <br /> NetSurf</h2>}
              reasonsData={constants.reasons}
              textClassName={'text-white'}
              isDark={true}
            />

            <CallCTA
              bgImg={data.ctaBg.childImageSharp.fluid.src}
              bgPerson={data.ctaPerson.childImageSharp.fluid}
            >
              <div className='text-md-left text-center'>
                <h2 className='mb-md-5 mb-2 d-nones d-md-blocks text-white'>  ,<br />   </h2>
                <p className='mb-md-5 mb-3 text-white'>         .</p>
                <div className='d-inline-block'>
                  <a href={'tel:070013888'} className='btn btn-primary green text-left d-inline-block mt-2 mr-md-2 mb-2'>   0700 13 888</a>
                </div>
                <div className='d-inline-block'>
                  <button className='btn btn-primary dark-blue'> </button>
                </div>
              </div>
            </CallCTA>

            <VerticalSteps
              steps={constants.verticalSteps}
            />

            <TestimonialSlider
              sectionTitle={<h2 className='text-white'> <br />  </h2>}
              opinions={constants.clientOpinions}
              isWhiteArrows={true}
              isDark={true}
            />

            <BottomCta
              bgImg={data.ctaBg.childImageSharp.fluid.src}
            >
              <h2 className='mb-md-5 mb-2 d-none d-md-block text-white'>    <br />   !</h2>
              <h2 className='mb-md-5 mb-2 d-block d-md-none text-white'>       !</h2>
              <p className='mb-md-5 mb-3 text-white'>      </p>
              <Link to={'/paketi'} className='btn btn-primary green'>  </Link>
            </BottomCta>

            <CollapsibleSection
              isDark={true}
              faqs={constants.faqs}
              sectionTitle={(
                <>
                  <h2 className='text-white' style={{ whiteSpace: 'pre-wrap' }}>  </h2>
                  <hr className='center blue' />
                </>
              )}
            />

            <div className='row d-flex justify-content-center'>
              <button className='btn btn-primary green' onClick={(e) => {setShow(true)}}>  </button>
            </div>
            <PopupChannels
              show={show}
              setShow={(v) => { setShow(v) }}
            />
          </Layout>
        </BackgroundWrapper>
      </div>
    </>
  )
}

const BackgroundWrapper = styled.div`
  background-image: url(${props => props.bg});
  background-position: top;
  background-size: cover;
  background-repeat: repeat-y;
`

export default BusinessPage;
